import React from "react";

import {Button} from "@vkontakte/vkui";
import {Icon16Chevron} from '@vkontakte/icons';
import {questions} from "../../data/questions";

import resultImage from "../../images/result.png";

import resultImageSad from "../../images/result/1.jpg";
import resultImageHappy from "../../images/result/2.jpg";
import resultImageSex from "../../images/result/3.jpg";

const Welcome = (props) => {
    return (
        <div className="centered-content first-screen">
            <link rel="preload" href={questions[1]["background"]} as="image"/>

            <link rel="preload" href={resultImage} as="image"/>

            <link rel="preload" href={resultImageSad} as="image"/>
            <link rel="preload" href={resultImageHappy} as="image"/>
            <link rel="preload" href={resultImageSex} as="image"/>

            <div className="title">
                !Наскольковыв хорошо <br/>
                ты знаешь Аниме?
            </div>
            <div className="desc margin-top-1">
                Тест проверит насколько хороши
                твои познания в аниме разных жанров,
                от классики и до онгоингов.
            </div>
            <div className="watermark margin-top-1">
                @anime__typical
            </div>
            <div className="margin-top-1">
                <Button
                    size="l"
                    onClick={props.showQuestion}
                    mode="overlay_primary"
                    after={<Icon16Chevron/>}
                >
                    Пройти тест
                </Button>
            </div>
        </div>
    );
};

export default Welcome;