import React, {useState} from 'react';

import * as CSS from "../css/css"

import Welcome from "./screens/welcome";
import Question from "./screens/question";
import Result from "./screens/result";

import backgroundImage from "../images/background.jpg";
import {View, Panel, ModalRoot, ModalCard, Avatar} from "@vkontakte/vkui";
import {joinGroup} from "../helpers/VK";

const Home = () => {
    const [screen, setScreen] = useState("welcome");
    const [popout, setPopout] = useState(null);
    const [alreadyShare, setAlreadyShare] = useState(false);
    const [alreadyFollow, setAlreadyFollow] = useState(false);
    const [helpConditionsAlready, setHelpConditionsAlready] = useState([]);
    const [activeModal, setActiveModal] = useState(null);
    const [resultShow, setResultShow] = useState(false);

    const [rightAnswers, setRightAnswers] = useState(0);

    const showQuestion = () => {
        setRightAnswers(0);
        setScreen("question");
    };

    const showResult = () => {
        setScreen("result");
    };

    const modalBack = () => {
        setActiveModal(null);
    };

    const followOnGroup = async () => {
        let follow = await joinGroup();

        setActiveModal(null);

        if (follow) {
            setAlreadyFollow(true);
        }

        resultInit(1);
    };

    const resultInit = (force = 0) => {
        if (alreadyFollow || force === 1) {
            setResultShow(true);
            setActiveModal(null);
        } else {
            setActiveModal("FOLLOW_CARD");
        }
    };

    const modal = (
        <ModalRoot
            activeModal={activeModal}
            onClose={modalBack}
        >
            <ModalCard
                id="FOLLOW_CARD"
                onClose={() => setActiveModal(null)}
                icon={<Avatar mode="default" src="https://sun9-25.userapi.com/c852024/v852024332/7d380/_qW0ADSK_GI.jpg?ava=1" size={72} />}
                header="Аниме"
                caption="Подписаться на сообщество?"
                actions={[{
                    title: 'Нет',
                    mode: 'secondary',
                    action: () => resultInit(1)
                }, {
                    title: 'Да',
                    mode: 'primary',
                    action: () => followOnGroup
                }]}
            />
        </ModalRoot>
    );

    return (
        <View activePanel="modals" modal={modal}>
            <Panel id="modals">
                {popout}
                <div className="container" style={CSS.background(backgroundImage)}>
                    {screen === "welcome" && <Welcome showQuestion={showQuestion}/>}
                    {screen === "question" && <Question
                        showResult={showResult} setPopout={setPopout}
                        rightAnswers={rightAnswers} setRightAnswers={setRightAnswers}
                        alreadyFollow={alreadyFollow} setAlreadyFollow={setAlreadyFollow}
                        helpConditionsAlready={helpConditionsAlready} setHelpConditionsAlready={setHelpConditionsAlready}
                    />}
                    {screen === "result" && <Result
                        alreadyShare={alreadyShare} setAlreadyShare={setAlreadyShare}
                        setAlreadyFollow={setAlreadyFollow} alreadyFollow={alreadyFollow}
                        rightAnswers={rightAnswers} repeat={showQuestion} setPopout={setPopout}
                        setActiveModal={setActiveModal} resultInit={resultInit}
                        resultShow={resultShow} />}
                </div>
            </Panel>
        </View>
    )
};

export default Home;
