import React, {useState} from 'react';

import '../css/popout.css';
import Confetti from "react-dom-confetti";
import {Button} from "@vkontakte/vkui";

const Popout = (props) => {
    const [confetti, setConfetti] = useState(false);

    if (props.showConfetti) {
        setTimeout(() => {
            setConfetti(true)
        }, 1);
    }

    let close = props.close || null;

    return (
        <div className="View__popout">
            <div className="Popout">
                <div className="Popout_container">
                    <div className="Popout_img">
                        {props.icon}
                        <Confetti className="confetti" active={confetti} config={{duration: 1500}}/>
                    </div>
                    <div className="Popout_title">{props.title}</div>
                    <div className="Popout_desc">{props.desc}</div>
                    <div className="Popout_buttons">
                        {close && <>
                            <Button
                                size="l"
                                mode="overlay_primary"
                                stretched
                                onClick={props.action}
                            >
                                Да
                            </Button>
                            <Button
                                className="no"
                                size="l"
                                mode="overlay_primary"
                                stretched
                                onClick={close}
                            >
                                Нет
                            </Button>
                        </>}
                        {!close && <Button
                            size="l"
                            mode="overlay_primary"
                            stretched
                            onClick={props.action}
                        >
                            OK
                        </Button>}
                    </div>
                </div>
            </div>
        </div>
    );
};

export default Popout;
