import React, {useState} from "react";

import {Button} from "@vkontakte/vkui";
import {Icon16Chevron, Icon28StoryOutline} from "@vkontakte/icons";

import resultImage from "../../images/result.png";

import resultImageSad from "../../images/result/1.jpg";
import resultImageHappy from "../../images/result/2.jpg";
import resultImageSex from "../../images/result/3.jpg";

import * as CSS from "../../css/css";

import {shareInStory, openApp, joinGroup} from "../../helpers/VK";
import Popout from "../../components/popout";

const Result = (props) => {
    let {
        rightAnswers,
        alreadyShare,
        setAlreadyShare,
        setPopout,
        resultShow,
        resultInit
    } = props;

    const levels = {
        0: {"name": "дилетант", "background": resultImageSad},
        1: {"name": "дилетант", "background": resultImageSad},
        2: {"name": "салага", "background": resultImageSad},
        3: {"name": "любитель", "background": resultImageSad},
        4: {"name": "юный кохай", "background": resultImageSad},
        5: {"name": "молодой семпай", "background": resultImageHappy},
        6: {"name": "мудрый сенсей", "background": resultImageHappy},
        7: {"name": "отаку", "background": resultImageHappy},
        8: {"name": "гуру аниме", "background": resultImageSex},
        9: {"name": "профессионал", "background": resultImageSex},
        10: {"name": "гений аниме", "background": resultImageSex},
    };

    async function share(repeat = 0) {
        await shareInStory(`${window.location.origin}/VkMiniApps_Anime_New_Test/results/${rightAnswers}.jpg`);

        setAlreadyShare(true);

        if (repeat === 1) {
            setPopout(null);
            props.repeat();
        }
    }

    const repeat = () => {
        if (alreadyShare) {
            props.repeat();
            return;
        }

        setPopout(<Popout
            icon={
                <Icon28StoryOutline className="popout-icon-padding" style={{color: "#3f8ae0"}} width={45}
                                    height={45}/>
            }
            title={null}
            desc={<>Поделиться результатом в истории?</>}
            action={() => share(1)}
            close={forceRepeat}
        />);
    };

    const forceRepeat = () => {
        setPopout(null);
        props.repeat();
    };

    const openTestMiniApp = async () => {
        await openApp(7559953);
    };

    let content;
    if (resultShow) {
        content = (
            <div className="result-screen">
                <div className="image" style={CSS.background(levels[rightAnswers]["background"])}/>
                <div className="margin-top-2 title">
                    Ты ответил на <span className="right-answers">{rightAnswers}</span>/10 <br/>
                    вопросов правильно!
                </div>
                <div className="margin-top-2 title">Твой уровень анимешника</div>
                <div className="margin-top-1 name">{levels[rightAnswers]["name"]}</div>
                <div className="margin-top-2 actions">
                    <Button onClick={() => share()} stretched size="l" mode="overlay_primary" after={<Icon16Chevron/>}>
                        Поделиться в истории
                    </Button>
                    <Button onClick={repeat} stretched size="l" mode="overlay_primary" after={<Icon16Chevron/>}>
                        Повторить
                    </Button>
                    <Button onClick={openTestMiniApp} stretched size="l" mode="overlay_primary"
                            after={<Icon16Chevron/>}>
                        Узнай кто ты из аниме
                    </Button>
                </div>
                <div className="margin-top-2">
                    @anime__typical
                </div>
            </div>
        )
    } else {
        content = (
            <>
                <div className="title">Тест пройден!</div>
                <div className="margin-top-1">
                    <Button
                        size="l"
                        mode="overlay_primary"
                        after={<Icon16Chevron/>}
                        onClick={resultInit}
                    >
                        Завершить тест
                    </Button>
                </div>
                <div className="watermark margin-top-1">@anime__typical</div>
            </>
        );
    }

    return (
        <>
            <div
                className={resultShow ? "centered-content first-screen" : "centered-content result-content first-screen"}
            >
                {content}
            </div>
            {!resultShow && <img className="image-result" src={resultImage} alt=""/>}
        </>
    );
};

export default Result;
