import bridge from "@vkontakte/vk-bridge";

export async function allowMessagesFromGroup() {
    return await bridge.send("VKWebAppAllowMessagesFromGroup", {"group_id": 86886389, "key": 1}).then(() => {
        return true;
    }).catch(() => {
        return false;
    });
}

export async function joinGroup() {
    return await bridge.send("VKWebAppJoinGroup", {"group_id": 86886389}).then(() => {
        return true;
    }).catch(() => {
        return false;
    });
}

export async function allowNotifications() {
    return await bridge.send("VKWebAppAllowNotifications", {}).then(() => {
        return true;
    }).catch(() => {
        return false;
    });
}

export async function openApp(appId) {
    return await bridge.send("VKWebAppOpenApp", {"app_id": appId}).then(() => {
        return true;
    }).catch(() => {
        return false;
    });
}

export async function shareInStory(url) {
    return await bridge.send("VKWebAppShowStoryBox", {
        "background_type": "image",
        "url": url,
        "locked": true,
        "attachment": {
            "text": "go_to",
            "type": "url",
            "url": "https://vk.com/app7559953"
        }
    }).then(() => {
        return true;
    }).catch(() => {
        return false;
    });
}